@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap");

h1,
p,
tr,
th,
button,
select {
  font-family: "Roboto", sans-serif;
  color: #333;
}

h1 {
  font-weight: 900;
}

button {
  width: 50%;
  height: 40px;
  font-weight: 700;
  background-color: #ee1b2e;
  color: white;
  border: 1px solid;
  border-radius: 8px;
  font-size: 20px;
}

button:hover {
  background-color: #af0d1c;
}

select {
  width: 100%;
  height: 40px;
  font-size: 18px;
  border: 1px solid black;
  border-radius: 8px;
  background-color: #f7f7f7;
  padding-left: 10px;
}

table {
  margin-top: 30px;
  width: 100%;
}

.app-container {
  max-width: 700px;
  margin: 0 auto;
  float: none;
}

.logo {
  width: 50px;
  padding-right: 5px;
}

.header-text {
  font-size: 22px;
  padding-left: 5px;
}

.header-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.route-select-container {
  padding-top: 50px;
}

.button-container {
  padding-top: 10px;
}

.stops-container {
  width: 100%;
  text-align: center;
}

.spinner {
  margin-top: 45px;
  margin-left: 50%;
}

